body{
  background-color: #FAFAFA;
}
.navigation-bar-container{
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-family: 'Poppins';
  font-size: small;
  height: 50px;

}
.intro-container{
  padding-top: 60px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;

}
.profile-image{
  border-radius: 100%;
  height: 150px;
  width: 145px;
  box-shadow: 0px 2px 3px;
}
.image-container{
  border-radius: 100%;
  width: 100px;
  height: 110px;
  background-color:#35E485;
  box-shadow: 0px 1px 3px;

}
.text-container{

  font-family: 'Poppins',sans serif;
  font-size: x-large;
  padding-top: 70px;

}
.first-name{
  position: left;
  display: inline;
  padding-left: 40px;
}
.second-name{
  display: inline;
  padding-left: 170px;
}
.green{
  color:#35E485;

}

.about-container{
  display: flex;
  padding-top: 50px;
}
.about-text-container{

  background-color: #35E485;
  min-height: 100px;
  width: 100%;
  color: white;
  text-align: center;
  font-family: 'Poppins',sans serif;
}
.text{
  padding-top: 10px;
  font-family: Poppins;
  font-size: x-small;
  display: inline-block;


}
.skill-container{
  padding-top: 40px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: row;

}

.skill-box{
  display: flex;
  width: 155px;
  height: 170px;
  background-color: white;
  box-shadow: 0px 1px 1px grey;
  color: black;
  font-family: 'Poppins',sans serif;
  text-align: center;
  align-items: center;
  justify-content: center;

  background-image: url("primary.svg"),url("Know More.svg") ;
  background-position:48px 140px ,70px 150px;
  background-repeat: no-repeat,no-repeat;
  background-size: 20px ,50px;





}

.padding{
  padding-top: 35px;
}
.contact-container{
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  font-family: 'Poppins',sans serif;
}
.contact-details{
  text-align: center;
}
.details{
  
  font-size: small;
}
.final-container{
  display: flex;
  padding-top: 70px;
  color:white;
  font-family: 'Poppins',sans serif;
  font-size:x-small;

}
.detail-container{
  min-width:100%;
  min-height: 100px;
  background-color: #35E485;
}
.final-text{
  padding-left: 20px;
  padding-top: 30px; 
}
.phone-image{
  height: 20px;
  width: 20px;
  padding-top: 10px;
}
.float-menu{
  position: fixed;
  bottom: 0%;
  width: 100%;
  min-height:40px;
  max-height:40px;
  background-color: white;
  border-radius: 10px;
  border-color: white;
  border:0.5px;
  box-shadow: 10px 10px 10x;
  border-style: solid;
}
.float-content{
  display: flex;
  height: 40px;
  width: auto;
  justify-content: space-around;
  align-items: center;
  vertical-align: middle;

}
.float-text{
  font-family: 'Poppins';
  font-size: small;


}
@media (min-width: 320px) and (max-width: 430px){
  .navigation-bar-container{
    visibility: hidden;
  }
  .intro-container{
    padding-top: 0px;
  }
  .float-text:hover{
    color: blue;
  }
}

@media(min-width: 1025px){
  .nav-item:hover{
    color:blue;
  }
  .image-container{
    height:220px;
    width:230px;
  }

  .float-menu{
    display: none;
  }
  .intro-container{
    flex-wrap: nowrap;
    display: inline-flex;
    padding-left: 15%;
  }
  .profile-image{
    height: 300px;
    width: 300px;
  }
  .text-container{
    display: inline;
    font-size: xx-large;
    position: absolute;
    top:15%;
    left: 50%;
  }
  .about-container{
    padding-top: 10%;

  }
  .about-text-container{
    max-height: 90px;
    min-height: 90px;
  }
  .text{
    font-size: small;
  }
  .skill-box{
    width: 300px;
    height: 300px;
    box-shadow: 1px 2px 2px lightgrey;
    font-size: x-large;
    background-position:80px 260px ,124px 263px;
    background-size: 70px 20px;
  }
  .contact-container{
    font-size: x-large;
  }
  .phone-image{
    height: 40px;
    width: 40px;
  }
  .details{
    font-size:large; 
    
  }
  .final-container{
    font-size: large;
  }
}
